  background-color: transparent;
  border: 2px solid var(--wp--preset--color--dark);
  padding: 14px 16px;
  font-size: var(--wp--preset--font-size--body-md, 16px);
  cursor: pointer;
  text-decoration: none;
  border-radius: var(--wp--custom--border-radius--050);
  gap: 6px;
  font-weight: bold;
  line-height: 100%;
  /* Rounded Button */
  /* Disabled */
}
.button.border-0, .wp-block-button.is-style-ghost .border-0.wp-block-button__link.wp-element-button, .wp-block-button.is-style-secondary .border-0.wp-block-button__link.wp-element-button, .wp-block-button.is-style-default .border-0.wp-block-button__link.wp-element-button {
  border: 0;
}
.button.button--rounded, .wp-block-button.is-style-ghost .button--rounded.wp-block-button__link.wp-element-button, .wp-block-button.is-style-secondary .button--rounded.wp-block-button__link.wp-element-button, .wp-block-button.is-style-default .button--rounded.wp-block-button__link.wp-element-button {
