.button:disabled, .wp-block-button.is-style-ghost .wp-block-button__link.wp-element-button:disabled, .wp-block-button.is-style-secondary .wp-block-button__link.wp-element-button:disabled, .wp-block-button.is-style-default .wp-block-button__link.wp-element-button:disabled {
  pointer-events: none;
}

/* -------------------------------------------------------------------------
 * BUTTON SIZES
 * ------------------------------------------------------------------------- */
/* Small Button */
.button--sm {
  padding: 11px 12px;
  font-size: var(--wp--preset--font-size--body-sm, 14px);
  /* Rounded Button */
}
.button--sm.button--rounded {
  width: 40px;
}

/* Large Button */
.button--lg {
  padding: 20px;
  font-size: var(--wp--preset--font-size--body-lg, 20px); /* Rounded Button */
}
.button--lg.button--rounded {
  width: 64px;
}

/* -------------------------------------------------------------------------
 * BUTTON VARIANTS
 * ------------------------------------------------------------------------- */
/* Primary Button */
.button--primary, .wp-block-button.is-style-default .wp-block-button__link.wp-element-button {
  background-color: var(--wp--preset--color--dark);
  color: var(--wp--preset--color--white);
  /* Primary Button Reversed */
}
.button--primary:hover, .wp-block-button.is-style-default .wp-block-button__link.wp-element-button:hover {
  background-color: transparent;
  color: var(--wp--preset--color--dark);
  text-decoration: underline;
}
.button--primary:focus, .wp-block-button.is-style-default .wp-block-button__link.wp-element-button:focus {
  box-shadow: 0px 0px 1px 2px #0997ff;
  text-decoration: underline;
}
.button--primary:active, .wp-block-button.is-style-default .wp-block-button__link.wp-element-button:active {
  background-color: var(--wp--preset--color--light-030);
  color: var(--wp--preset--color--dark);
  border-color: var(--wp--preset--color--light-030);
}
.button--primary:disabled, .wp-block-button.is-style-default .wp-block-button__link.wp-element-button:disabled {
  background-color: var(--wp--preset--color--light-020);
  color: var(--wp--preset--color--dark-060);
  border-color: var(--wp--preset--color--light-020);
}
.button--primary.button--reversed, .wp-block-button.is-style-default .button--reversed.wp-block-button__link.wp-element-button {
  background-color: var(--wp--preset--color--white);
  color: var(--wp--preset--color--dark);
  border-color: var(--wp--preset--color--white);
}
.button--primary.button--reversed:hover, .wp-block-button.is-style-default .button--reversed.wp-block-button__link.wp-element-button:hover {
  background-color: var(--wp--preset--color--dark);
  color: var(--wp--preset--color--white);
  text-decoration: underline;
}
.button--primary.button--reversed:active, .wp-block-button.is-style-default .button--reversed.wp-block-button__link.wp-element-button:active {
  background-color: var(--wp--preset--color--dark-080);
  color: var(--wp--preset--color--white);
}
.button--primary.button--reversed:disabled, .wp-block-button.is-style-default .button--reversed.wp-block-button__link.wp-element-button:disabled {
  background-color: var(--wp--preset--color--dark-090);
  color: var(--wp--preset--color--dark-060);
  border-color: var(--wp--preset--color--dark-090);
}

/* Secondary Button */
.button--secondary, .wp-block-button.is-style-ghost .wp-block-button__link.wp-element-button, .wp-block-button.is-style-secondary .wp-block-button__link.wp-element-button {
  background-color: transparent;
  color: var(--wp--preset--color--dark);
  border-color: var(--wp--preset--color--dark);
  /* Secondary Button Reversed */
}
.button--secondary:hover, .wp-block-button.is-style-ghost .wp-block-button__link.wp-element-button:hover, .wp-block-button.is-style-secondary .wp-block-button__link.wp-element-button:hover {
