  color: var(--wp--preset--color--white);
}
.button--secondary:disabled, .wp-block-button.is-style-ghost .wp-block-button__link.wp-element-button:disabled, .wp-block-button.is-style-secondary .wp-block-button__link.wp-element-button:disabled {
