  color: var(--wp--preset--color--dark-060);
  border-color: var(--wp--preset--color--light-030);
}
.button--secondary.button--reversed, .wp-block-button.is-style-ghost .button--reversed.wp-block-button__link.wp-element-button, .wp-block-button.is-style-secondary .button--reversed.wp-block-button__link.wp-element-button {
  background-color: transparent;
  color: var(--wp--preset--color--white);
  border-color: var(--wp--preset--color--white);
}
.button--secondary.button--reversed:hover, .wp-block-button.is-style-ghost .button--reversed.wp-block-button__link.wp-element-button:hover, .wp-block-button.is-style-secondary .button--reversed.wp-block-button__link.wp-element-button:hover {
