}
.button--secondary.button--reversed:active, .wp-block-button.is-style-ghost .button--reversed.wp-block-button__link.wp-element-button:active, .wp-block-button.is-style-secondary .button--reversed.wp-block-button__link.wp-element-button:active {
  background-color: var(--wp--preset--color--light-020);
  border-color: var(--wp--preset--color--light-020);
  color: var(--wp--preset--color--dark);
}
.button--secondary.button--reversed:disabled, .wp-block-button.is-style-ghost .button--reversed.wp-block-button__link.wp-element-button:disabled, .wp-block-button.is-style-secondary .button--reversed.wp-block-button__link.wp-element-button:disabled {
  background-color: transparent;
  color: var(--wp--preset--color--dark-060);
  border-color: var(--wp--preset--color--dark-060);
}

/* Ghost Button */
.button--ghost {
  background-color: transparent;
  color: var(--wp--preset--color--dark);
  border-color: transparent;
  /* Ghost Button Reversed */
}
.button--ghost:hover {
  background-color: var(--wp--preset--color--light-020);
  color: var(--wp--preset--color--dark);
  text-decoration: underline;
}
.button--ghost:focus {
  box-shadow: 0px 0px 1px 2px #0997ff;
  text-decoration: underline;
}
.button--ghost:active {
  background-color: var(--wp--preset--color--light-030);
  color: var(--wp--preset--color--dark);
}
.button--ghost:disabled {
  background-color: transparent;
  color: var(--wp--preset--color--dark-060);
  border-color: transparent;
}
.button--ghost.button--reversed {
  background-color: transparent;
  color: var(--wp--preset--color--white);
  border-color: transparent;
}
