  color: var(--wp--preset--color--dark-060);
  border-color: transparent;
}

a:hover {
  text-decoration: underline;
}
a:focus {
  box-shadow: var(--wp--custom--box-shadow--focus);
  text-decoration: underline;
}

p > a[target=_blank]:after {
  display: inline-block;
  content: "\f35d";
  font-family: var(--fa-style-family, "Font Awesome 6 Pro");
  font-weight: inherit;
  margin-left: 4px;
  font-size: 90%;
}

